<script setup>
import { ref, computed, onMounted } from 'vue'
import Sticker from './Sticker.vue'
import Button from './Button.vue'

const stickers = ref([
	{
		text: 'Исключить условие про ДМС для иностранца',
		isCorrect: true,
		isOnBoard: false,
		rotate: '0deg',
	},
	{
		text: 'Включить правило о выплатах взносов на обязательное социальное страхование на случай временной нетрудоспособности и в связи с материнством по ГПД',
		isCorrect: true,
		isOnBoard: false,
		rotate: '0deg',
	},
	{
		text: 'Включить гарантии мобилизованным и их семьям',
		isCorrect: true,
		isOnBoard: false,
		rotate: '0deg',
	},
	{
		text: 'Включить гарантию для мобилизованных брать отпуск в удобное для них время в течение шести месяцев после службы',
		isCorrect: true,
		isOnBoard: false,
		rotate: '0deg',
	},
	{
		text: 'Включить правила расчета квоты при трудоустройстве инвалидов',
		isCorrect: true,
		isOnBoard: false,
		rotate: '0deg',
	},
	{
		text: 'Приезжать на работу в форменной одежде',
		isCorrect: false,
		rotate: '0deg',
	},
	{
		text: 'Заранее предупреждать руководство о досрочном выходе из отпуска по уходу за ребенком',
		isCorrect: false,
		rotate: '0deg',
	},
	{
		text: 'При устройстве на работу приносить документы, которые не названы в ТК РФ',
		isCorrect: false,
		rotate: '0deg',
	},
	{
		text: 'Не работать в другом месте по совместительству или подрабатывать по ГПД',
		isCorrect: false,
		rotate: '0deg',
	},
	{
		text: 'Не трудоустраиваться к конкурентам в течение определенного времени после увольнения',
		isCorrect: false,
		rotate: '0deg',
	},
])

const getRandomRotate = () => {
	const randomDegree = Math.random() * 20 - 10 // Range between -10 and 10
	return `${randomDegree}deg`
}

const correctCount = computed(() => {
	return stickers.value.filter(item => item.isCorrect && item.isOnBoard).length
})

const handleDrop = event => {
	event.preventDefault()

	const draggedSticker = event.dataTransfer.getData('sticker')
	const stickerIndex = parseInt(draggedSticker, 10)

	if (stickers.value[stickerIndex].isCorrect) {
		const dropZone = event.target.getBoundingClientRect()
		const x = event.clientX - dropZone.left
		const y = event.clientY - dropZone.top

		stickers.value[stickerIndex].position = { x, y }
		stickers.value[stickerIndex].isOnBoard = true
		stickers.value[stickerIndex].rotate = getRandomRotate()
	}
}

const restart = () => {
	location.reload()
}

onMounted(() => {
	const shuffleArray = array => {
		for (let i = array.length - 1; i > 0; i--) {
			const j = Math.floor(Math.random() * (i + 1))
			;[array[i], array[j]] = [array[j], array[i]]
		}
	}

	shuffleArray(stickers.value)
})
</script>

<template>
	<div class="container">
		<div class="container__board">
			<img src="../assets/probkovaya_doska.png" alt="" />
			<div
				dropzone="move"
				class="drop__zone"
				@dragover.prevent
				@drop="handleDrop"
			></div>
		</div>

		<div class="container__stickers">
			<Sticker
				v-for="(sticker, index) in stickers"
				:key="index"
				:text="sticker.text"
				:isOnBoard="sticker.isOnBoard"
				:rotate="sticker.rotate"
				:draggable="true"
				@dragstart="
					event => event.dataTransfer.setData('sticker', index.toString())
				"
				:style="
					sticker.isOnBoard
						? {
								position: 'absolute',
								left: `${sticker.position.x}px`,
								top: `${sticker.position.y}px`,
								width: 'auto',
								height: '20vw',
						  }
						: {}
				"
			></Sticker>
		</div>
		<Transition mode="out-in">
			<div v-if="correctCount >= 5" class="container__popup">
				<div class="popup">
					<p>ВЫ ОТЛИЧНО СПРАВИЛИСЬ. ХОТИТЕ ПРОЙТИ ТРЕНИРОВКУ ЕЩЕ РАЗ?</p>
				</div>
				<div class="popup__btn">
					<Button @click="restart" font-size="1.2em" padding="1vw 5vw"
						>Начать заново</Button
					>
				</div>
			</div>
		</Transition>
	</div>
</template>

<style scoped>
.container {
	position: relative;
}

.container__board {
	z-index: -1;
}

.container__board img {
	width: 100%;
	height: 70vw;
	z-index: -2;
}

.container__stickers {
	margin: 0 1vw;
	display: grid;
	grid-template-columns: repeat(3, calc(100vw / 3 - 2.5vw));
	gap: 2vw;
}

.drop__zone {
	position: absolute;
	width: 100vw;
	height: 72vw;
	top: 0;
	display: grid;
	grid-template-columns: repeat(3, calc(100vw / 3 - 2.5vw));
	gap: 2vw;
}

.container__popup {
	position: absolute;
	top: 27vw;
	width: 60vw;
	left: 50vw;
	transform: translateX(-50%);
	padding: 5vw;
	background-color: #fafafa;
	border-radius: 4vw;
}

.popup {
	color: #89be2c;
	font-size: 1.2em;
	text-align: center;
}

.popup__btn {
	margin-top: 3vw;
	display: flex;
	justify-content: center;
}
</style>
